import React, { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { gsap } from "gsap";
import "./Home.css";

const Home = () => {
  const navigate = useNavigate();
  const backgroundRef = useRef(null);
  const textRef = useRef(null);
  const logoRef = useRef(null);
  const bannerRef = useRef(null);
  const launchCardRef = useRef(null);

  useEffect(() => {
    gsap.fromTo(
      backgroundRef.current,
      { opacity: 0 },
      { opacity: 1, duration: 2, ease: "power2.inOut" }
    );

    gsap.fromTo(
      logoRef.current,
      { y: -30, opacity: 0, scale: 0.9 },
      { y: 0, opacity: 1, scale: 1, duration: 1.5, ease: "back.out(1.7)" }
    );

    if (textRef.current) {
      const children = Array.from(textRef.current.children);
      gsap.fromTo(
        children,
        { y: 30, opacity: 0 },
        { y: 0, opacity: 1, duration: 1.2, stagger: 0.2, ease: "power2.out", delay: 0.5 }
      );
    }

    gsap.fromTo(
      bannerRef.current,
      { scale: 0.8, opacity: 0 },
      { scale: 1, opacity: 1, duration: 1.5, ease: "elastic.out(1, 0.5)", delay: 0.3 }
    );

    gsap.fromTo(
      launchCardRef.current,
      { y: 50, opacity: 0 },
      { y: 0, opacity: 1, duration: 1.5, ease: "power2.out", delay: 0.6 }
    );
  }, []);

  return (
    <div className="home-container">
      <div className="background-image" ref={backgroundRef}></div>

      <div className="content-overlay">
        {/* Grand Opening Banner */}
        <div style={{cursor:'pointer'}} className="grand-opening-banner" ref={bannerRef} onClick={() => navigate("/invite")}>
          🚀 GRAND OPENING - JOIN US FOR THE CELEBRATION! 🎉
        </div>

     

        {/* Main Content */}
        <div className="text-content" ref={textRef}>
          <h2 className="collection-tag">SPRING COLLECTION 2025</h2>
          <div className="logo-container" ref={logoRef}>
            <img className="logo" src="../saharsarawhite.png" alt="Logo" />
          </div>
          <div className="button-container" onClick={() => navigate("/products")}>
            <button className="primary-btn">Explore Collection</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
