import React, { useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import CardActions from '@mui/material/CardActions';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import ZoomInIcon from '@mui/icons-material/ZoomIn';
import Rating from '@mui/material/Rating';
import Box from '@mui/material/Box';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import Chip from '@mui/material/Chip';
import { motion } from 'framer-motion';

const ProductCard = ({ product }) => {
  const [favorite, setFavorite] = useState(false);
  const [openQuickView, setOpenQuickView] = useState(false);
  const [rating] = useState(4.5);

  const handleQuickViewOpen = () => setOpenQuickView(true);
  const handleQuickViewClose = () => setOpenQuickView(false);
  const handleFavoriteClick = () => setFavorite(!favorite);

  const handleWhatsApp = (images) => {
    const phoneNumber = "919188802189"; // Replace with actual number
    const message = `Hello, I'm interested in purchasing these products:\n`;
  
    // Check if images is an array, then join URLs with new lines
    const imageUrls = Array.isArray(images) ? images.join("\n") : images;
  
    // Construct the WhatsApp URL with images and message
    const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message + imageUrls)}`;
  
    // Open WhatsApp in a new tab
    window.open(whatsappUrl, "_blank");
  };
  
  // Example usage
  //handleWhatsApp(["https://example.com/image1.jpg", "https://example.com/image2.jpg"]);
  

  const formattedPrice = new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR',
    maximumFractionDigits: 0
  }).format(product.price);

  return (
    <>
      <motion.div whileHover={{ y: -10, boxShadow: "0px 10px 20px rgba(0, 0, 0, 0.1)", transition: { duration: 0.3 } }}>
        <Card sx={{ maxWidth: 345, margin: '15px', border: '1px solid #e0e0e0', borderRadius: '8px' }}>
          {product.tag && (
            <Chip label={product.tag} color={product.tag === "SALE" ? "error" : "primary"} sx={{ position: 'absolute', top: 10, right: 10 }} />
          )}
          <Box sx={{ position: 'relative' }}>
            <CardMedia component="img" height="260" image={product.image} alt={product.name} sx={{ transition: 'transform 0.5s ease', '&:hover': { transform: 'scale(1.05)' } }} />
            <Box sx={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', backgroundColor: 'rgba(0,0,0,0.2)', display: 'flex', justifyContent: 'center', alignItems: 'center', opacity: 0, transition: 'opacity 0.3s ease', '&:hover': { opacity: 1 } }}>
              <Button variant="contained" startIcon={<ZoomInIcon />} onClick={handleQuickViewOpen} sx={{ backgroundColor: 'white', color: '#333', '&:hover': { backgroundColor: '#D4AF37', color: 'white' } }}>Quick View</Button>
            </Box>
          </Box>
          <CardContent>
            <Typography gutterBottom variant="h5" sx={{ fontFamily: 'Playfair Display, serif' }}>{product.name}</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <Rating value={rating} precision={0.5} size="small" readOnly />
              <Typography variant="body2" sx={{ ml: 1 }}>({Math.floor(Math.random() * 100) + 10} reviews)</Typography>
            </Box>
            <Typography variant="h6" sx={{ fontWeight: 'bold' }}>{formattedPrice}</Typography>
          </CardContent>
          <CardActions sx={{ display: 'flex', justifyContent: 'space-between', padding: '0 16px 16px' }}>
            <Box>
              <IconButton aria-label="add to favorites" onClick={handleFavoriteClick} color={favorite ? "error" : "default"}><FavoriteIcon /></IconButton>
              <IconButton aria-label="share"><ShareIcon /></IconButton>
            </Box>
            <Button variant="contained" startIcon={<ShoppingCartIcon />} onClick={()=>handleWhatsApp(product)} sx={{ backgroundColor: '#D4AF37', '&:hover': { backgroundColor: '#b8860b' } }}>Add to Cart</Button>
          </CardActions>
        </Card>
      </motion.div>
      <Dialog open={openQuickView} onClose={handleQuickViewClose} maxWidth="md" fullWidth>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' } }}>
            <Box sx={{ flex: 1 }}><img src={product.image} alt={product.name} style={{ width: '100%', objectFit: 'cover' }} /></Box>
            <Box sx={{ flex: 1, p: 3 }}>
              <Typography variant="h4" sx={{ mb: 2 }}>{product.name}</Typography>
              <Typography variant="h5" sx={{ color: '#D4AF37', mb: 3, fontWeight: 'bold' }}>{formattedPrice}</Typography>
              <Button variant="contained" fullWidth startIcon={<ShoppingCartIcon />} onClick={()=>handleWhatsApp(product.image)} sx={{ backgroundColor: '#D4AF37', '&:hover': { backgroundColor: '#b8860b' } }}>Order via WhatsApp</Button>
            </Box>
          </Box>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default ProductCard;
