import React, { useState, useEffect } from 'react';
import ProductCard from '../components/ProductCard';
import { motion } from 'framer-motion';
import productsData from './products.json'

const Products = () => {
  const [products, setProducts] = useState([]);

  useEffect(() => {
  console.log("fffffffffff",productsData)
    setProducts(productsData);
    setFilteredProducts(productsData)
  }, []);

  const [filteredProducts, setFilteredProducts] = useState(products);
  const [activeFilter, setActiveFilter] = useState('all');
  const [isLoading, setIsLoading] = useState(true);
  const [sortOrder, setSortOrder] = useState('default');

  // Simulate loading for smooth animation entrance
  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false);
    }, 800);
  }, []);

  const filterProducts = (category) => {
    setActiveFilter(category);
    if (category === 'all') {
      setFilteredProducts(products);
    } else {
      setFilteredProducts(products.filter(product => product.category === category));
    }
  };

  const handleSort = (order) => {
    setSortOrder(order);
    let sortedProducts = [...filteredProducts];
    
    if (order === 'price-asc') {
      sortedProducts.sort((a, b) => a.price - b.price);
    } else if (order === 'price-desc') {
      sortedProducts.sort((a, b) => b.price - a.price);
    } else if (order === 'name') {
      sortedProducts.sort((a, b) => a.name.localeCompare(b.name));
    }
    
    setFilteredProducts(sortedProducts);
  };

  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        type: "spring",
        stiffness: 100
      }
    },
    hover: {
      y: -10,
      scale: 1.03,
      transition: {
        type: "spring",
        stiffness: 400,
        damping: 10
      }
    }
  };

  const fadeIn = {
    hidden: { opacity: 0 },
    visible: { opacity: 1, transition: { duration: 1 } }
  };

  return (
    <div className="products-page">
      {/* Hero section with parallax effect */}
      <div className="hero-section" style={{
        height: '50vh',
        backgroundImage: "url('/images/back.jpg')",
        backgroundAttachment: 'fixed',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        position: 'relative',
        
      }}>
        <div style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          backgroundColor: 'rgba(0,0,0,0.4)'
        }}></div>
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          style={{
            textAlign: 'center',
            color: 'white',
            zIndex: 1,
            padding: '0 20px'
          }}
        >
          <h1 style={{ 
            fontSize: '3rem', 
            fontWeight: 'bold',
            marginBottom: '1rem',
            fontFamily: 'Playfair Display, serif'
          }}>Exquisite Designer Sarees</h1>
          <p style={{ 
            fontSize: '1.2rem',
            maxWidth: '600px',
            margin: '0 auto',
            fontFamily: 'Poppins, sans-serif'
          }}>Discover our handcrafted collection of premium silk sarees for every occasion</p>
        </motion.div>
      </div>

      <div style={{ 
        maxWidth: '1200px', 
        margin: '0 auto', 
        padding: '40px 20px',
       
       
      }}>
        {/* Filter and Sort Controls */}
        <motion.div 
          variants={fadeIn}
          initial="hidden"
          animate="visible"
          className="controls"
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            marginBottom: '30px',
            flexWrap: 'wrap',
            gap: '20px'
          }}
        >
          <div className="filter-buttons" style={{ display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
            {['all', 'wedding', 'festive', 'casual', 'party'].map(category => (
              <button
                key={category}
                onClick={() => filterProducts(category)}
                className={`filter-btn ${activeFilter === category ? 'active' : ''}`}
                style={{
                  padding: '8px 16px',
                  border: 'none',
                  backgroundColor: activeFilter === category ? '#D4AF37' : '#f8f8f8',
                  color: activeFilter === category ? 'white' : '#333',
                  borderRadius: '30px',
                  cursor: 'pointer',
                  transition: 'all 0.3s ease',
                  fontFamily: 'Poppins, sans-serif',
                  textTransform: 'capitalize'
                }}
              >
                {category}
              </button>
            ))}
          </div>
          
          <div className="sort-options">
            <select 
              onChange={(e) => handleSort(e.target.value)}
              value={sortOrder}
              style={{
                padding: '8px 16px',
                border: '1px solid #e0e0e0',
                borderRadius: '4px',
                backgroundColor: 'white',
                fontFamily: 'Poppins, sans-serif'
              }}
            >
              <option value="default">Sort By</option>
              <option value="price-asc">Price: Low to High</option>
              <option value="price-desc">Price: High to Low</option>
              <option value="name">Name</option>
            </select>
          </div>
        </motion.div>

        {/* Products Grid with Animation */}
        {isLoading ? (
          <div style={{ 
            display: 'flex', 
            justifyContent: 'center', 
            alignItems: 'center',
            height: '300px'
          }}>
            <div className="loader"></div>
          </div>
        ) : (
          <motion.div
            variants={containerVariants}
            initial="hidden"
            animate="visible"
            style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
              gap: '30px'
            }}
          >
            {filteredProducts.map(product => (
              <motion.div
                key={product.id}
                variants={itemVariants}
                whileHover="hover"
                className="product-card-wrapper"
              >
                <ProductCard 
                  product={product} 
                  animated={true}
                />
              </motion.div>
            ))}
          </motion.div>
        )}

        {/* Empty state message */}
        {filteredProducts.length === 0 && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            style={{
              textAlign: 'center',
              padding: '40px',
              color: '#666'
            }}
          >
            <h3>No sarees found in this category</h3>
            <p>Please try another filter or check back soon for new arrivals</p>
          </motion.div>
        )}

        {/* Newsletter signup */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.5, duration: 0.8 }}
          style={{
            marginTop: '60px',
            padding: '40px',
            backgroundColor: '#f9f5ff',
            borderRadius: '8px',
            textAlign: 'center'
          }}
        >
          <h3 style={{ marginBottom: '20px', fontFamily: 'Playfair Display, serif' }}>
            Be the first to know about new arrivals
          </h3>
          <div style={{ 
            display: 'flex', 
            maxWidth: '500px', 
            margin: '0 auto',
            gap: '10px' 
          }}>
            <input
              type="email"
              placeholder="Enter your email"
              style={{
                flex: 1,
                padding: '12px 16px',
                border: '1px solid #e0e0e0',
                borderRadius: '4px',
                fontFamily: 'Poppins, sans-serif'
              }}
            />
            <button style={{
              padding: '12px 24px',
              backgroundColor: '#D4AF37',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              fontFamily: 'Poppins, sans-serif'
            }}>
              Subscribe
            </button>
          </div>
        </motion.div>
      </div>

      {/* Add some CSS for animations */}
      <style jsx>{`
        @import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Poppins:wght@300;400;500&display=swap');

        .products-page {
          font-family: 'Poppins', sans-serif;
        }

        .filter-btn:hover {
          background-color: #f0d98b !important;
          transform: translateY(-2px);
        }

        .loader {
          border: 4px solid #f3f3f3;
          border-top: 4px solid #D4AF37;
          border-radius: 50%;
          width: 40px;
          height: 40px;
          animation: spin 1s linear infinite;
        }

        @keyframes spin {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }

        /* Additional styles for the ProductCard component if needed */
        /* Note: You might need to update your ProductCard component to accept animated prop */
      `}</style>
    </div>
  );
};

export default Products;